import * as data from "../data/data.js";
// upload csv file and convert in object

let csv = data.data;
let fileList = new Array;
let haveFile = false;



const handleFiles = (files) => {
  // Check for the various File API support.
  if (window.FileReader) {
    // FileReader are supported.
    var reader = new FileReader();

    // Read file into memory as UTF-8
    reader.readAsText(files[0]);

    //inserto el nombre del fichero en el DOM
    document.getElementById('fileName').insertAdjacentHTML('beforeend',files[0].name);

    // Handle errors load
    reader.onerror = function (evt) {
      if(evt.target.error.name == "NotReadableError") {
        alert("Canno't read file !");
      }
    }
    reader.onload = function (event) {
      csv = event.target.result;
      csv = processDataAsObj(csv);
    };

  } else {
    alert('FileReader are not supported in this browser.');
  }
};

function processDataAsObj(csv){
    var allTextLines = csv.split(/\r\n|\n/);
    var lines = [];

    //first line of csv
    var keys = allTextLines.shift().split(';');

    while (allTextLines.length) {
        var arr = allTextLines.shift().split(';');
        var obj = {};
        for(var i = 0; i < keys.length; i++){
            obj[keys[i]] = arr[i];
	}
        lines.push(obj);
    }
        return lines;
}

//_____________________________________________app
const cleanColor = '#D3D6C7';
const fase0 = '#405027';
const fase1 = '#5B8122';
const fase2 = '#7BB520';
const fase3 = '#93DE1F';
const nuevaNormalidad = '#B4FC45';

const colorCountry = (country, fase) => {
  document.getElementById(country).setAttribute('fill', fase);
}

const paintMap = (csv) => {
  for (let cnt of csv) {
    switch (cnt.fase) {
      case '0':
          colorCountry(cnt.ID, fase0);
        break;
      case 0 :
          colorCountry(cnt.ID, fase0);
        break;
      case '1':
          colorCountry(cnt.ID, fase1);
        break;
      case 1:
          colorCountry(cnt.ID, fase1);
        break;
      case '2':
          colorCountry(cnt.ID, fase2);
        break;
      case 2:
          colorCountry(cnt.ID, fase2);
        break;
      case '3':
          colorCountry(cnt.ID, fase3);
        break;
      case 3:
          colorCountry(cnt.ID, fase3);
        break;
      case '4':
          colorCountry(cnt.ID, nuevaNormalidad);
        break;
      case 4:
          colorCountry(cnt.ID, nuevaNormalidad);
        break;
      default:
        colorCountry(cnt.ID, cleanColor);
        console.log('Error [Not match] !');
    }
  }
}

paintMap(csv);

//_____________________________________________exe

const btnAbled = (target) => {
  const btn = document.getElementById(target);
  if ( btn.classList.contains('btn-disabled')) {
    btn.classList.remove('btn-disabled');
    btn.classList.add('btn-abled');
  } else {
    btn.classList.remove('btn-abled');
    btn.classList.add('btn-disabled');
  }
}

/*--BOT√ìN Seleccionar CSV*/

const btnFileSelector = document.querySelector('#csvFileInput');
btnFileSelector.addEventListener('change', (event) => {
  fileList = event.target.files;
  haveFile = true;
  handleFiles(fileList);
  console.log('se ha selecionado un fichero');
  if (haveFile){
    btnAbled('btn-upload-label');
  }
});

/*--BOT√ìN Cargar*/

const btnCargar = document.querySelector('#btn-upload');
btnCargar.addEventListener('click', (event) => {
  paintMap(csv);
  console.log('se ha cargado el fichero');
});

